




































.list-container li {
  border-bottom: 1px solid #eee;
}

.list-title {
  font-size: 2.6rem;
  font-weight: 400;
  color: #404040;
  margin-top: 0;
}

.list-abstract {
  font-size: 1.6rem;
  color: #919191;
  font-weight: 200;
}

.list-updated {
  font-family: "Comic Sans MS", curslve, sans-serif;
  font-size: 1.8rem;
  color: #8b8b8b;
  padding: 5px 0;
}

.list-container li a {
  padding: 1rem 1.5rem;
  display: block;
  -webkit-transition: all .3s;
  transition: all .3s;
  margin:0;
}

.list-container li a:hover {
  background-color: #ededed;
}

@media screen and (max-width: 768px) {
  .list-title {
    font-size: 2.2rem;
  }

  .list-updated {
    font-size: 1.6rem;
  }
}



