  margin: 2rem auto;
}

.tagset ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.tagset li {
  margin: .3rem;
}

.tagset li a {
  display: block;
  cursor: pointer;
  padding: .3rem 1.6rem;
  margin: 0;
  border: 1px solid #d2d2d2;
  border-radius: 2rem;
  color: #f7f7f7;
  background-color: rgb(196, 196, 196);
  -webkit-transition: all .4s;
  transition: all .4s;
}

.tagset li a:hover, .tagset .tagset-active {
  background-color: #efefef;
  color: #424242;
}
@media screen and (max-width: 768px) {
  .tagset li a {
    padding:.1rem 1rem;
  }
  .tagset li {
    margin: .2rem;
  }
}



